html {
  font-size: 20px !important;
  overflow: hidden;
}

body {
  font-size: 1rem;
  margin: 0;
  padding: 0;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: #000000;
}

h2 {
  font-weight: 900;
  font-size: 2rem;
  padding: 3.15rem 0 1.1rem 0;
  text-align: center;
}

ul {
  margin: 0 !important;
  list-style: none;
  padding: 0 !important;
}

li {
  list-style: none;
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
  padding: 0;
}

button {
  outline: none;
  background-color: transparent;
  border: none;
  cursor: pointer;
}

section {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-self: center;
}

/* ==========================     폰트      ===================== */
@font-face {
  font-family: "ChosunCentennial";
  src: url("./component/assets/fonts/ChosunCentennial_otf.otf")
    format("truetype");
}
@font-face {
  font-family: "ChosunGs";
  src: url("./component/assets/fonts/ChosunGs.TTF") format("truetype");
}

@font-face {
  font-family: "Gmarket-Bold";
  src: url("./component/assets/fonts/GmarketSansTTFBold.ttf") format("truetype");
}
/* ==========================     반응형 작업      ===================== */

@media screen and (max-width: 1799px) {
  /* 데스크탑 */
  html {
    font-size: 18px !important;
  }
}

@media screen and (max-width: 1199px) {
  html {
    font-size: 16px !important;
  }
}

/* 테블릿 */
@mixin tablet {
  @media screen and (max-width: 899px) {
    html {
      font-size: 14px !important;
    }
  }
}

/* 모바일 */
@mixin mobile {

  @media (max-width: 480px) {
    font-size: 1rem;
  }
  @media screen and (max-width: 599px) {
    html {
      font-size: 12px !important;
      background-size: cover;
      background-position: center center;
    }
  }
}
@media screen and (max-width: 479px) {
  html {
    font-size: 10px !important;
    background-size: cover;
    background-position: center center;
  }
}
